<template>
  <div class="posts__wrapper single-post">
    <div class="post" v-if="this.$store.getters.auth.isAuthenticated">
        <div class="mb-2">
          <input type="text" v-model="name" placeholder="Имя">
        </div>
        <div class="mb-2">
          <input type="text" v-model="email" placeholder="Email">
        </div>
        <div class="mb-2">
          <input type="password" v-model="password" placeholder="Пароль">
        </div>
        <div class="mb-2">
          <input type="password" v-model="spassword" placeholder="Повторите пароль">
        </div>

        <button v-if="this.$store.getters.auth.isAuthenticated" class="cursor-pointer" v-on:click="updateUser()">Сохранить</button>
    </div>
  </div>
</template>

<script>
export default {
    name: 'EditUser',
    data: () => ({
      name: '',
      spassword: '',
      email: '',
      password: ''
    }),
    methods: {
      updateUser: function () {
        let data = {
          name: this.name,
          spassword: this.spassword,
          email: this.email,
          password: this.password
        }
        this.axios.put(`http://new-viva-test/api/users/${this.$route.params.id}`, JSON.stringify(data), {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${ localStorage.getItem('accessToken') }`
          }
        })
        .then(() => {
          this.$router.push('/users')
        })
      },
      getUser: function () {
        this.axios.get(`http://new-viva-test/api/users/${this.$route.params.id}`, {
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${ localStorage.getItem('accessToken') }`
          }
        })
        .then((response) => {
          this.name = response.data.data.name
          this.email = response.data.data.email
        })
      },
    },
    computed: {
        applicationAuth: function () {
            return this.$store.getters.auth.isAuthenticated
        }
    },
    watch: {
        applicationAuth(newVal) {
            if(newVal === true) this.getUser(this.$route.params.id)
        }
    },
    mounted() {
        if (this.$auth.isAuthenticated()) this.getUser(this.$route.params.id)
    }
}
</script>

<style>
    .post{
        padding: 15px;
        margin: 15px 0;
        border-radius: 4px;
        background: #f7f7f7;
        transition: 0.2s;
        text-align: left;
    }

    .post:hover {
        background: #f3f3f3;
    }

    .post .title {
        font-weight: 200;
        margin: 0;
        font-size: 30px;
        text-decoration: none;
        color: black;
    }

    .text-right{
        text-align: right;
    }

    .cursor-pointer {
      cursor: pointer;
    }

    .text-left {
        text-align: left;
    }

    .editor {
      width: 100%;
      height: auto;
      resize: none;
      border-radius: 4px;
      border: transparent;
      font-family: sans-serif;
      box-sizing: border-box;
      padding: 10px;
    }

    .fs-h1 {
      font-size: 20px;
      min-height: 90px;
    }

    .fs-p{
      min-height: 490px;
    }
</style>